.site-list {
	display: flex;
	list-style: none;
	margin: 0;
	padding: 0;

	font-size: 17px;
	font-weight: 700;

	&__link {
		display: block;
		padding: 35px;
		color: $black;
		text-decoration: none;

		@media screen and (min-width: 768px) and (max-width: 1199px) {
			padding: 18px;
		}
	}
}