.title {
	margin: 0;
	padding: 0;

	font-family: $heading;
	font-size: 27px;
	line-height: 30px;
	font-weight: 700;

	@media screen and (min-width: 768px) {
		font-size: 43px;
		line-height: 50px;
	}

	@media screen and (min-width: 1200px) {
		font-size: 53px;
		line-height: 57px;
	}
}