.price {
	font-size: 15px;
	font-weight: 700;
	line-height: 24px;
	color: $black;
	margin: 0;

	@media screen and (min-width: 768px) {
		font-size: 17px;
	}

	&.--gray {
		color: $gray2;
	}

	&.--size_big {
		font-size: 17px;

		@media screen and (min-width: 768px) {
			font-size: 20px;
		}
	}
}